import {mapGetters, mapState} from 'vuex';

let page = 1;
let is_loading = false;
let is_no_more = false;

export default {
    name: 'app-comments',
    props: {
        goodsId: Number,
        url: {
            type: String,
            default() {
                return '';
            }
        },
        showType: {
            type: String,
            default() {
                return 'detail'
            }
        },
        reachBottom: Number,
    },
    watch: {
        goodsId: {
           handler() {
               this.loadData();
           }
        },
        reachBottom: {
          handler(){
              if (is_no_more) return;
              this.loadData();
          }
        }
    },
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
    },
    methods: {
        loadData() {
            if (this.mall.setting.is_comment == 0) return;
            if (is_loading) return;
            is_loading = true;
            if (this.showType !== 'detail') {
                uni.showLoading({
                    title: '加载中'
                });
            }
            this.$request({
                url: this.url ? this.url : this.$api.goods.comments_list,
                data: {
                    goods_id: this.goodsId,
                    page: page,
                    status: this.status,
                }
            }).then(response => {
                is_loading = false;
                uni.hideLoading();
                if (response.code === 0) {
                    this.commentCount = response.data.comment_count;
                    if (page === 1) {
                        this.list = [];
                    }
                    let list = response.data.comments;
                    if (list.length > 0) {
                        if (this.showType === 'detail') {
                            list = list.splice(0, 2)
                        }
                        this.list = [...this.list, ...list];
                        page++;
                    } else {
                        is_no_more = true;
                    }
                }
            }).catch(() => {
                is_loading = false;
                uni.hideLoading();
            });
        },
        goto() {
            uni.navigateTo({
                url: `/pages/comments/comments?goods_id=${this.goodsId}`
            })
        },
        clickStatus(status) {
            this.status = status;
            page = 1;
            is_no_more = false;
            this.loadData();
        },
        imgPreview(index, pic_index) {
            if (this.list && this.list[index] && this.list[index].pic_url && this.list[index].pic_url.length > 0) {
                uni.previewImage({
                    current: pic_index,
                    urls: this.list[index].pic_url
                });
            }
        },
    },
    data() {
        return {
            commentCount: [],
            list: [],
            status: 0,
        };
    },
    created() {
        page = 1;
        is_loading = false;
        is_no_more = false;
    },
 
    mounted() {
        if (this.goodsId) this.loadData();
    },
}
